export default {
	data() {
		return {
			inputValue: ''
		};
	},
	methods: {
		login() {
			if (this.inputValue.trim()) {
				this.$store.dispatch('getUserInfo', {
					accessToken: this.inputValue,
					callback: function() {
						uni.navigateBack({
							delta: 1
						});
					}
				});
			} else {
				uni.showToast({
					title: 'access token 不能为空',
					icon: 'none'
				});
			}
		},
		scanCode: function() {
			var self = this;
			uni.scanCode({
				scanType: ['qrCode'],
				success: function(res) {
					self.inputValue = res.result;
					self.login();
				},
				fail(res) {
					uni.showModal({
						content: res.errMsg
					});
				}
			});
		}
	}
};
